@import "src/common/colorPalette.scss";
@import "../../common/mixin.scss";

.login-container {
    text-align: center;
    padding: 0 10%;
    height: fit-content;

    @include breakpoint(medium) {
        box-shadow: 0 0 25px rgba(0, 0, 0, 0.25);
        margin: 4% 5% 0 66%;
        padding: 0 4% 5%;
    }


    .login {
        &-heading {
            text-align: center;
            font-size: 50px;
            color: $secondary-color;
            margin-top: 0;
            padding-top: 25px;

            @media only screen and (min-width: 600px) {
                padding-top: 50px;
                font-size: 40px;
            }
        }

        &-subheading {
            text-align: center;
            font-size: 30px;
            color: $secondary-color;

            @media only screen and (min-width: 600px) {
                padding-top: 50px;
                font-size: 40px;
            }
        }

        &-forgot-password {
            text-decoration: none;
            color: $secondary-color;
            font-size: 20px;
        }

        &-sign-up {
            &-heading {
                font-weight: bold;
                color: $secondary-color;
                font-size: 30px;
            }

            &-user {
                color: $secondary-color;
                font-size: 20px;

                &:hover {
                    text-decoration: none;
                }
            }

            &-vendor {
                color: $secondary-color;
                font-size: 20px;

                &:hover {
                    text-decoration: none;
                }
            }
        }
    }

    .sign-in-checkbox {
        display: block;
        text-align: center;
    }
}
