@import 'src/common/colorPalette.scss';

.vendor-signup-container {
    text-align: center;
    padding: 0 10%;
    height: fit-content;

    @media only screen and (min-width: 600px) {
        box-shadow: 0 0 25px rgba(0, 0, 0, 0.25);
        margin: 0 33.33%;
        padding: 0 4% 5%;
    }

    .vendor-signup {
        &-heading {
            text-align: center;
            font-size: 50px;
            color: $secondary-color;
            margin-top: 0;
            padding-top: 25px;

            @media only screen and (min-width: 600px) {
                padding-top: 50px;
                font-size: 40px;
            }
        }

        &-subheading {
            border-bottom: 1px solid $secondary-color;
            color: $secondary-color;
            font-size: 30px;
            text-align: center;
            padding-bottom: 30px;

            @media only screen and (min-width: 600px) {
                padding-top: 50px;
                font-size: 39px;
            }
        }

        &-form-subheading {
            color: $secondary-color;
            font-size: 17px;
            text-align: center;

            @media only screen and (min-width: 600px) {
                font-size: 22px;
            }
        }
    }
}